*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --bl: #101010;
  --dark: #34312d;
  --dark-grey: #2e2e2e;
  --1-grey: #4c4c4c;
  --2-grey: #777;
  --light-grey: #b3b3b3;
  --3-grey: #e5e5e5;
  --bg: #f8f8f8;
  --white: #fff;
  --orange: #f57520;
  --or2: #eb6b16;
  --or3: #e1610c;
  --or4: #d75702;
  --red: #f53a20;
  --banner: #131313;
  --green: #4d932c;
  --font-family: "Inter", sans-serif;
  --second-family: "Bebas Neue Cyrillic", sans-serif;
  --third-family: "Open Sans", sans-serif;
  --font3: "Bebas Neue", sans-serif;
  --font4: "Montserrat", sans-serif;
}

body {
  min-height: 100vh;
  min-width: 100%;
  font-family: var(--font-family);
  color: var(--white);
  flex-direction: column;
  margin: auto;
  font-size: 16px;
  font-weight: 400;
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-family: var(--second-family);
  font-size: 60px;
  font-weight: 400;
}

@media (min-width: 768px) {
