.brands {
  height: 120px;
  background-color: var(--white);
  white-space: nowrap;
  margin-bottom: 48px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.brands:before, .brands:after {
  content: "";
  width: 100px;
  height: 100%;
  z-index: 2;
  position: absolute;
  top: 0;
}

.brands:before {
  background: linear-gradient(to left, rgba(255, 255, 255, 0), #fff);
  left: 0;
}

.brands:after {
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff);
  right: 0;
}

.brands div {
  width: max-content;
  align-items: center;
  gap: 0 24px;
  animation: animate 35s linear infinite;
  display: flex;
}

.brands:hover div {
  animation-play-state: paused;
}

@keyframes animate {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

